<template>
	<div class="app-content" v-if="!!Object.keys(content).length">
		<div class="container">
			<div class="page-title">
				<h1>Главная страница</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()" :disabled="saveInProgress">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
				<fieldset>
                    <legend>Вступительная секция</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Баннер</label>
                            <div class="input-group">
                                <span class="input-group-text">URL</span>
                                <input type="text" v-model="content.heroSection.bannerUrl" class="form-control">
                            </div>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.heroSection.title" :content="content.heroSection.title"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Описание</label>
                            <InputLangGroup v-model="content.heroSection.description" :content="content.heroSection.description" :isTextarea="true"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Кнопка 1</label>
                            <InputLangGroup v-model="content.heroSection.btnText1" :content="content.heroSection.btnText1"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Кнопка 2</label>
                            <InputLangGroup v-model="content.heroSection.btnText2" :content="content.heroSection.btnText2"/>
                        </div>
					</div>
				</fieldset>
                <fieldset>
                    <legend>Список категорий</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.categories.title" :content="content.categories.title"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Текст кнопки</label>
                            <InputLangGroup v-model="content.categories.moreBtn" :content="content.categories.moreBtn"/>
                        </div>
					</div>
				</fieldset>
                <fieldset>
                    <legend>Кратко о компании</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.aboutSection.title" :content="content.aboutSection.title"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Описание</label>
                            <InputLangGroup v-model="content.aboutSection.description" :content="content.aboutSection.description" :isTextarea="true"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Кнопка</label>
                            <InputLangGroup v-model="content.aboutSection.btnText" :content="content.aboutSection.btnText"/>
                        </div>
					</div>
				</fieldset>
                <fieldset>
                    <legend>Партнеры</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.partners.title" :content="content.partners.title"/>
                        </div>
                        <div class="col-4" v-for="(partner, i) in content.partners.list" :key="i">
                            <label class="form-label">Логотип {{ i + 1 }}</label>
                            <div class="input-group">
                                <span class="input-group-text">URL</span>
                                <input type="text" v-model="content.partners.list[i]" class="form-control">
                            </div>
                        </div>
					</div>
				</fieldset>
			</div>
		</div>
	</div>
</template>

<script>
import InputLangGroup from '../components/InputLangGroup.vue';

export default {
    data() {
        return {
            content: '',
            saveInProgress: false,
            isSavedSuccess: false
        }
    },
    components: {
        InputLangGroup
    },
    computed: {
        pageContent() {
            return this.$store.getters['getHomepageContent'];
        }
    },
    watch: {
        pageContent(value) {
            this.content = value;
        }
    },
    methods: {
        savePage() {
            this.saveInProgress = true;
            this.$store.dispatch('saveHomePage', this.content)
                .then(() => {
                    this.saveInProgress = false;
                    this.isSavedSuccess = true;
                    setTimeout(() => {
                        this.isSavedSuccess = false;
                    }, 3000);
                });
        }
    },
    created() {
        this.content = this.pageContent;
    },
    mounted() {

    }
};
</script>